.panel-image {
  background-repeat: no-repeat;
  background-size: cover!important;
  -moz-background-size: cover!important;
  -webkit-background-size: cover!important;
  -o-background-size: cover!important;
  background-position: center;
  overflow: hidden;
}

.bg-primary {
  background-color: get-color(primary);
}
.bg-secondary {
  background-color: get-color(secondary);
}
.bg-success {
  background-color: get-color(success);
}
.bg-warning {
  background-color: get-color(warning);
}
.bg-alert {
  background-color: get-color(alert);
}
.bg-light-grey {
  background-color: get-color(light-gray);
}
.bg-medium-grey {
  background-color: get-color(medium-gray);
}
.bg-dark-grey {
  background-color: get-color(dark-gray);
}

.filter {
  position: absolute;
  top:0px;
  left:0px;
  width: 100%;
  height: 100%;
  &.filter-black {
    background-color: rgba(0,0,0,0.8);
  }
  &.filter-white {
    background-color: rgba(255,255,255,0.8);
  }
  &.filter-primary {
    background-color: rgba(get-color(primary),0.8);
  }
  &.filter-secondary {
    background-color: rgba(get-color(secondary),0.8);
  }
  &.filter-warning {
    background-color: rgba(get-color(warning),0.8);
  }
  &.filter-alert {
    background-color: rgba(get-color(alert),0.8);
  }
}


